"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";

const Calculator = () => {
  const [expression, setExpression] = useState("");
  const [history, setHistory] = useState<string[]>([]);

  const handleButtonClick = (value: string) => {
    // If current display shows error, clear it on new input (except C)
    if (expression === "Error" && value !== "C") {
      setExpression("");
    }

    if (value === "C") {
      setExpression("");
    } else if (value === "⌫") {
      setExpression(prev => prev.slice(0, -1));
    } else if (value === "=") {
      if (expression && expression !== "Error") {
        try {
          // Replace special symbols with JS operators
          const sanitizedExpression = expression
            .replace(/×/g, "*")
            .replace(/÷/g, "/");
          
          const result = eval(sanitizedExpression);

          // Check for invalid numeric results
          if (isNaN(result) || !isFinite(result)) {
            throw new Error("Invalid calculation");
          }
          
          const calcResult = result.toString();
          setHistory(prev => [`${expression} = ${calcResult}`, ...prev]);
          setExpression(calcResult);
        } catch (err) {
          setExpression("Error");
        }
      }
    } else {
      // For operators, prevent consecutive operators
      const lastChar = expression.slice(-1);
      const operators = ["+", "-", "×", "÷"];
      
      if (operators.includes(value) && operators.includes(lastChar)) {
        // Replace the last operator with the new one
        setExpression(prev => prev.slice(0, -1) + value);
      } else {
        setExpression(prev => prev + value);
      }
    }
  };

  const clearHistory = () => {
    setHistory([]);
  };

  return (
    <div className="w-full max-w-md mx-auto bg-card rounded-lg shadow-lg p-6">
      <h1 className="text-2xl font-bold mb-6 text-center text-foreground">
        Calculator
      </h1>
      
      {/* Display Area */}
      <div className="mb-6 p-4 bg-background border rounded-lg">
        <div className="text-right text-3xl font-mono text-foreground min-h-[3rem] flex items-center justify-end">
          {expression || "0"}
        </div>
      </div>
      
      {/* Button Grid */}
      <div className="grid grid-cols-4 gap-3 mb-6">
        {/* Row 1 */}
        <Button 
          variant="destructive" 
          onClick={() => handleButtonClick("C")}
          className="h-12 text-lg font-semibold"
        >
          C
        </Button>
        <Button 
          variant="secondary" 
          onClick={() => handleButtonClick("⌫")}
          className="h-12 text-lg"
        >
          ⌫
        </Button>
        <Button 
          variant="outline" 
          disabled
          className="h-12 opacity-30"
        >
          
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("÷")}
          className="h-12 text-lg font-semibold"
        >
          ÷
        </Button>
        
        {/* Row 2 */}
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("7")}
          className="h-12 text-lg"
        >
          7
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("8")}
          className="h-12 text-lg"
        >
          8
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("9")}
          className="h-12 text-lg"
        >
          9
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("×")}
          className="h-12 text-lg font-semibold"
        >
          ×
        </Button>
        
        {/* Row 3 */}
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("4")}
          className="h-12 text-lg"
        >
          4
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("5")}
          className="h-12 text-lg"
        >
          5
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("6")}
          className="h-12 text-lg"
        >
          6
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("-")}
          className="h-12 text-lg font-semibold"
        >
          -
        </Button>
        
        {/* Row 4 */}
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("1")}
          className="h-12 text-lg"
        >
          1
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("2")}
          className="h-12 text-lg"
        >
          2
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("3")}
          className="h-12 text-lg"
        >
          3
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("+")}
          className="h-12 text-lg font-semibold"
        >
          +
        </Button>
        
        {/* Row 5 */}
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick("0")}
          className="col-span-2 h-12 text-lg"
        >
          0
        </Button>
        <Button 
          variant="outline" 
          onClick={() => handleButtonClick(".")}
          className="h-12 text-lg"
        >
          .
        </Button>
        <Button 
          onClick={() => handleButtonClick("=")}
          className="h-12 text-lg font-semibold"
        >
          =
        </Button>
      </div>
      
      {/* History Panel */}
      {history.length > 0 && (
        <div className="border-t pt-4">
          <div className="flex justify-between items-center mb-3">
            <h2 className="text-lg font-semibold text-foreground">History</h2>
            <Button 
              variant="ghost" 
              size="sm"
              onClick={clearHistory}
              className="text-sm"
            >
              Clear History
            </Button>
          </div>
          <div className="max-h-32 overflow-y-auto bg-muted rounded-lg p-3">
            <ul className="space-y-1">
              {history.map((item, index) => (
                <li 
                  key={index} 
                  className="text-sm text-muted-foreground font-mono py-1 border-b border-border last:border-b-0"
                >
                  {item}
                </li>
              ))}
            </ul>
          </div>
        </div>
      )}
    </div>
  );
};

export default Calculator;
